<template>
    <div class="container">
        <group title="">
            <datetime :value.sync="datetime1" placeholder="" :min-year=1960 :max-year=2040 format="YYYY-MM" @on-change="change" title="" year-row="{value}"
                      month-row="{value}" confirm-text="" cancel-text=""></datetime>
        </group>

        <group title="">
            <datetime :value.sync="datetime2" placeholder="" :min-year=1960 :max-year=2040 format="YYYY-MM" @on-change="change" title="" year-row="{value}"
                      month-row="{value}" confirm-text="" cancel-text=""></datetime>
        </group>

        <group title="">
            <address @on-hide="logHide" @on-show="logShow" :title="title" :value.sync="city" :list="addressData" placeholder=""></address>
            <cell title="value" :value="city | json"></cell>
        </group>

        <group title="">
            <selector placeholder="" :options="list"></selector>
        </group>

        <group title="">
            <x-input title="message" placeholder="Input"></x-input>
        </group>

        <group title="">
            <x-input title="" name="username" placeholder="" is-type="china-name"></x-input>
        </group>

        <group title="">
            <x-input title="" name="mobile" placeholder="" keyboard="number" is-type="china-mobile"></x-input>
        </group>
    </div>
</template>
<script>
import { Datetime, Group, Address, AddressChinaData, Cell, Selector, XInput } from '../components'
import value2name from '../filter/value2name'

export default {
    components: {
        Datetime,
        Group,
        Address,
        Cell,
        Selector,
        XInput
    },
    data () {
        return {
            datetime1: '',
            datetime2: '',
            title: '',
            city: [],
            addressData: AddressChinaData,
            list: [{key: 'gd', value: ''}, {key: 'gx', value: ''}]
        }
    },
    methods: {
        change (value) {
            console.log('change', value)
        },
        clearValue (value) {
            this.$data.value6 = ''
            console.log('clear')
        },
        setToday (value) {
            let now = new Date()
            let cmonth = now.getMonth() + 1
            let day = now.getDate()
            if (cmonth < 10) cmonth = '0' + cmonth
            this.$data.value7 = now.getFullYear() + '-' + cmonth + '-' + day
            console.log('set today ok')
        },
        changeData () {
            this.value2 = ['430000', '430400', '430407']
        },
        getName (value) {
            return value2name(value, AddressChinaData)
        },
        logHide (str) {
            console.log('on-hide', str)
        },
        logShow (str) {
            console.log('on-show')
        }
    }
}
</script>
<style lang="less" scoped>

</style>


